import React from 'react';
import './Main.css';

import Brand from './images/brand.png';
import caretDown from './images/caret-down.png';
import caretUp from './images/caret-up.png';

class Main extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isValid: null,
            address: '',
            statusMsg: '',
            loading: false,
            caret: caretDown,
            statusCode: 'N/A',
            server: 'N/A',
            date: 'N/A',
            lastModif: 'N/A',
            contentType: 'N/A',
            body: '',
            textCopied: ''
        }
        this.handleChange = this.handleChange.bind(this);
        this.handleClick = this.handleClick.bind(this);
        this.fetchAddress = this.fetchAddress.bind(this);
        this.changeCaret = this.changeCaret.bind(this);
        this.copyBody = this.copyBody.bind(this);
        this.changeStatus = this.changeStatus.bind(this);
    }

    fetchAddress(addy) {
        const requestOptions = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ address: addy })
        };

      

        fetch('http://localhost:9000/checkIfValid', requestOptions)
        .then(response => response.json())
        .then(data => this.changeStatus(data));
        

        fetch('http://localhost:9000/additionalDetails', requestOptions)
        .then(response => response.json())
        .then(data => this.setState({
            loading: false,
            statusCode: data.statusCode,
            server: data.headers.server,
            date: data.headers.date,
            lastModif: data.headers['last-modified'],
            contentType: data.headers['content-type'],
            body: data.body
        }));
        // input all into state ^^^
        
        
        


       

        this.setState({
            loading: false
        })
  
      
    }

    changeStatus(data) {
        this.setState({
            isValid: data
        });
        if(this.state.isValid) {
            this.setState({
                statusMsg: 'Website is up and running!'
            })
        } else {
            this.setState({
                statusMsg: 'Website is currently down/or doesnt exist!'
            })
        }
        console.log('DATa:' + data);
    }

    handleChange(e) {
        this.setState({
            address: e.target.value
        });
    }

    handleClick() {
        this.setState({
            loading: true,
            statusMsg: '',
            isValid: false,
            textCopied: ''
        });

        setTimeout(() => {
            this.fetchAddress(this.state.address);
        }, 3000);
       
    }

    changeCaret() {
        if(this.state.caret == caretDown) {
            this.setState({
                caret: caretUp
            });
        } else if(this.state.caret == caretUp) {
            this.setState({
                caret: caretDown
            })
        }
    }

    copyBody() {
        navigator.clipboard.writeText(this.state.body);
        this.setState({
            textCopied: 'Successfully copied.'
        })
    }

    render() {
        return (
            <div id="main-wrapper">
                <div id="main-header-container">
                    <img id="main-header-img" src={Brand} height="152px"/>
                    <div id="main-header-text-flex">
                        <h1 id="main-header-title">Website Ping Checker</h1>
                        <p id="main-header-desc">type in any website address you desire<span className="desc-extra">, and weâ€™ll check<br></br>
    whether its user browsable!</span></p>
                    </div>
                </div>
                <div id="main-status-checker-container">
                    <div id="main-status-checker-box">
                        <div id="main-status-nav-bar">
                            <h1>Check the status of your website</h1>
                        </div>
                        <div id="main-status-fields-flex">
                            <input onChange={this.handleChange} placeholder="Enter a website address" type="text" id="main-status-checker-input"/>
                            <button onClick={this.handleClick} id="main-status-checker-btn" type="text">Ping</button>

                            <p id="tip-msg">* Website addresses must be absolute</p>
                            {this.state.loading ? <div className="loader"><div></div><div></div><div></div></div> : <div></div>}
                            {this.state.isValid == true ? <h3 id="main-status-result-success">{this.state.statusMsg}</h3> : <h3 id="main-status-result-fail">{this.state.statusMsg}</h3>}
                            
                            
                        </div>
                    </div>
                    {this.state.isValid ? <div id="additional-details-wrapper">
                        <button onClick={this.changeCaret} id="additionalDetails-btn" type="button">View Additional Details <img id="caret" src={this.state.caret}/></button>
                        {this.state.caret == caretUp ? <div id="additional-details-content">
                            <li className="detail-item">Status Code: <span className="bold-detail">{this.state.statusCode}</span></li>
                            <li className="detail-item">Server: <span className="bold-detail">{this.state.server}</span></li>
                            <li className="detail-item">Date: <span className="bold-detail">{this.state.date}</span></li>
                            <li className="detail-item">Last Modified: <span className="bold-detail">{this.state.lastModif}</span> </li>
                            <li className="detail-item">Content-Type: <span className="bold-detail">{this.state.contentType}</span> </li>
                            <li className="detail-item">Body: <button onClick={this.copyBody} id="copy-btn" type="button">Copy</button> <span className="green-text">{this.state.textCopied}</span></li>
                        </div> : <div> </div> }
                        
                    </div> : <div> </div>}
                </div>
            </div>
        );
    }
}

export default Main;